import React from 'react';
import useFrontDispatch from "../../../hooks/common/useFrontDispatch";
import {ProjectVO} from "iso/src/store/bootstrap/repos/projectsCURD";
import {
    IonButtons,
    IonCard,
    IonCardContent,
    IonContent,
    IonFooter,
    IonHeader,
    IonIcon,
    IonItem,
    IonLabel,
    IonPage,
    IonTitle,
    IonToggle,
    IonToolbar
} from '@ionic/react';
import {useHistory} from "react-router"
import * as Icons from "ionicons/icons"

import {useProject} from "../../../hooks/useProject";
import BackToProjectButton from "../../controls/BackToProjectButton";
import usePreferences, {PrefKey} from "../../../hooks/common/usePreferences";
import usePrefKey from "../../../hooks/common/usePrefKey";

const SettingsOption = ({prefKey, name, icon}: {name: string, icon, isAdminOnly?: boolean, prefKey: PrefKey}) => {

    const [value, setValue] = usePrefKey(prefKey)
    return <IonItem>
        {icon && <IonIcon slot={'start'} icon={icon}></IonIcon>}

        <IonLabel>
            {name}
        </IonLabel>
        <IonToggle slot={'end'} checked={Boolean(value)}
                   onIonChange={e => setValue(e.detail.checked)}/>

    </IonItem>
}

const SettingsScreen = (props) => {
    return (<IonPage>
            <IonHeader>
                <IonToolbar>
                    <IonButtons slot="start">
                        <BackToProjectButton/>
                    </IonButtons>
                    <IonTitle></IonTitle>
                </IonToolbar>
            </IonHeader>
            <IonContent>
                <IonCard>
                    <SettingsOption name={' '} icon={Icons.moon} prefKey={'darkMode'}/>
                    <SettingsOption name={'MaterialUI '} icon={Icons.balloon} prefKey={'ionModeMaterial'}/>
                    <SettingsOption name={' '} icon={Icons.flashlight} prefKey={'animatedColors'}/>
                </IonCard>
                <IonCard>
                    <SettingsOption name={' '} icon={Icons.radio} prefKey={'toastsEventsFeed'}/>
                    <IonCardContent>
                                  
                        
                    </IonCardContent>
                </IonCard>
                <IonCard> <SettingsOption name={' FPS'} icon={Icons.skull} prefKey={'showFPS'}/>
                    <SettingsOption name={' '} icon={Icons.skull} prefKey={'showExamples'}/>
                    <IonCardContent>
                         
                    </IonCardContent>
                </IonCard>

            </IonContent>
            <IonFooter>
                <IonToolbar>

                </IonToolbar>
            </IonFooter>
        </IonPage>
    )
}

export default SettingsScreen;
