import axios from "axios";
import {Action} from "redux";
import {isArray} from "@sha/utils";

export default async () => {
    const axiosInstance = axios//.create({})
    /*{baseURL: window.location.hostname.includes('localhost')
            ? 'http://localhost:9180'
            :''})*/
    return {
        login: async (payload: { email, password, remember }) => {
            const response = await axiosInstance.post('/api/v1/user/login', payload)

            return response.data
        },
        fetchBootstrap: async (payload) => {
            const response = await axiosInstance.post('/api/v1/bootstrap', payload)

            return response.data
            /**const response = await axios.post('/api/v1/bootstrap')
             const state = response.data
             return state as any as YPAppState*/
            // return fetchBootstrapMock()
        },
        loadPro: async (id) => {
            const response = await axiosInstance.post('/api/v1/project/'+id, )

            return response.data
            /**const response = await axios.post('/api/v1/bootstrap')
             const state = response.data
             return state as any as YPAppState*/
            // return fetchBootstrapMock()
        },
        pushCommands: async (events: Action[] | Action) => {
            const array = isArray(events) ? events : [events]
            const body = {
                events: array.map(e => ({
                    ...e,
                    sourceUserId: 1,
                    storeGuid: 1,
                }))
            }
            const response = await axiosInstance.post('/api/v1/push-commands', body)
            return response.data
        }
    }

}
